@import url(https://fonts.googleapis.com/css?family=Work+Sans&display=swap);
body {
  margin: 0;
  font-family: 'Work Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #eceff1;
  color: #4d4d4d;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.Pokecard {
    margin: 0 2rem 20px 2rem;
    padding: 8px 0 0 0;
    box-shadow: 7px 10px 10px -5px rgba(0, 0, 0, .56);
    text-align: center;
    background-color: white;
    border-radius: 5px;
}

.Pokecard-title {
    margin: 5px 0;
}

.Pokecard-data {
    font-size: 70%;
    margin: 4px;
}

.Pokecard-image { 
    background-color: #eceff1;
    border-radius: 4px;
    margin: 5px auto;
    width: 75%;
}

.Pokecard img {
     width: 80%;
     transition: all ease 500ms;
}

.Pokecard img:hover {
    transform: scale(1.2);
    opacity: 0.8;
}


.Pokedex {
    text-align: center;
}

.Pokedex-cards {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.Pokedex-winner {
    color: #4caf50;
    margin-bottom: 0;
}

.Pokedex-loser {
    color: #e91e63;
    margin-bottom: 0;
}

.Pokedex-exp {
    margin-top: 8px;
}
